/*=======================*/
/* Redactor 
/*=======================*//*=======================*//*=======================*/
/*
	Icon font
*/
@font-face {
  font-family: 'RedactorFont';
  src: url('../js/vendor/redactor/redactor-font.eot');
}
@font-face {
  font-family: 'RedactorFont';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'), url(data:application/font-woff;charset=utf-8;base64,) format('woff');
  font-weight: normal;
  font-style: normal;
}
/* =Selection
-----------------------------------------------------------------------------*/
.redactor_box ::selection {
  background: #ffff9e;
}
.redactor_box ::-moz-selection {
  background: #ffff9e;
}
.redactor_box img::selection {
  background: transparent;
}
.redactor_box img::-moz-selection {
  background: transparent;
}
/*
	BOX
*/
.redactor_box {
	color: @gray-dark !important;
  position: relative;
  overflow: visible;
  //.get-down;
  .push-down-sm;
}
.redactor_box iframe {
  display: block;
  margin: 0;
  padding: 0;
  border: 1px solid #eee;
}
.redactor_box textarea {
  position: relative;
  display: block;
  overflow: auto;
  margin: 0;
  padding: 0;
  width: 100%;
  outline: none;
  border: none;
  background-color: #222;
  box-shadow: none;
  color: #ccc;
  font-size: 13px;
  font-family: Menlo, Monaco, monospace, sans-serif;
  resize: none;
}
.redactor_box textarea:focus {
  outline: none;
}
.redactor_box,
.redactor_box textarea {
  z-index: auto !important;
}
.redactor_box_fullscreen {
  z-index: 99 !important;
}
#redactor_modal_overlay,
#redactor_modal,
.redactor_dropdown {
  z-index: 100 !important;
}

/*
	LINK TOOLTIP
*/
.redactor-link-tooltip {
  position: absolute;
  z-index: 49999;
  padding: 10px;
  line-height: 1;
  display: inline-block;
  background-color: #000;
  color: #555 !important;
}
.redactor-link-tooltip,
.redactor-link-tooltip a {
  font-size: 12px;
  font-family: Arial, Helvetica, Verdana, Tahoma, sans-serif;
}
.redactor-link-tooltip a {
  color: #ccc;
  margin: 0 5px;
  text-decoration: none;
}
.redactor-link-tooltip a:hover {
  color: #fff;
}
/*
	IMAGE BOX
*/
#redactor-image-box img {
  width: 100%;
}
.redactor_editor {
  position: relative;
  overflow: auto;
  margin: 0 !important;
  padding: 10px 20px;
  padding-bottom: 5px;
  outline: none;
  background: @gray-light;
  box-shadow: none !important;
  white-space: normal;
  border: 1px solid @gray-med-light;
}
.redactor_editor:focus {
  outline: none;
}
.redactor_editor div,
.redactor_editor p,
.redactor_editor ul,
.redactor_editor ol,
.redactor_editor table,
.redactor_editor dl,
.redactor_editor blockquote,
.redactor_editor pre {
  .font-size-xxs;
}
.redactor_editor a {
  //color: #15c !important;
  //text-decoration: underline !important;
}
.redactor_editor .redactor_placeholder {
  color: #999 !important;
  display: block !important;
}
/*
	TYPOGRAPHY
*/
.redactor_editor object,
.redactor_editor embed,
.redactor_editor video,
.redactor_editor img {
  width: 100%;
  max-width: 300px;
  .centered;
}
.redactor_editor video,
.redactor_editor img {
  height: auto;
}
.redactor_editor div,
.redactor_editor p,
.redactor_editor ul,
.redactor_editor ol,
.redactor_editor table,
.redactor_editor dl,
.redactor_editor blockquote,
.redactor_editor pre {
  margin: 0;
  margin-bottom: 0px !important;
  border: none;
  background: none !important;
  box-shadow: none !important;
}
.redactor_editor iframe,
.redactor_editor object,
.redactor_editor hr {
  margin-bottom: 15px !important;
}
.redactor_editor blockquote {
  margin-left: 1.5em !important;
  padding-left: 0 !important;
  color: #777;
  font-style: italic !important;
}
.redactor_editor ul,
.redactor_editor ol {
  padding-left: 2em !important;
}
.redactor_editor ul ul,
.redactor_editor ol ol,
.redactor_editor ul ol,
.redactor_editor ol ul {
  margin: 2px !important;
  padding: 0 !important;
  padding-left: 2em !important;
  border: none;
}


/*
	TOOLBAR
*/
.redactor_toolbar {
	.clearfix;
  position: relative;
  top: 0;
  left: 0;
  margin: 0 !important;
  padding: 0 !important;
  font-size: 14px !important;
  z-index: 3;
}

.redactor_toolbar.redactor-toolbar-overflow {
  overflow-y: auto;
  height: 29px;
  white-space: nowrap;
}
.redactor_toolbar.redactor-toolbar-external {
  z-index: 999;
  box-shadow: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
body .redactor_air .redactor_toolbar {
  padding-right: 2px !important;
}
.redactor_toolbar li {
  vertical-align: top;
  display: inline-block;
  margin: 0 !important;
  padding: 0 !important;
  outline: none;
  list-style: none !important;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
.redactor_toolbar li a {
  display: block;
  text-align: center;
  padding: 9px 10px;
  outline: none;
  border: none;
  zoom: 1;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
.redactor_toolbar li a:hover {
  outline: none;
  background-color: @brand-button-dark;
  color: #fff;
}
.redactor_toolbar li a:hover i:before {
  color: #fff;
}
.redactor_toolbar li a:active,
.redactor_toolbar li a.redactor_act {
  outline: none;
  background-color: @gray-med-light;
  color: #444;
}
.redactor_toolbar li a.redactor-btn-image {
  width: 14px;
  height: 14px;
  background-position: center center;
  background-repeat: no-repeat;
}
.redactor_button_disabled {
  filter: alpha(opacity=30);
  -moz-opacity: 0.3;
  opacity: 0.3;
}
.redactor_button_disabled:hover {
  outline: none;
  background-color: transparent !important;
  cursor: default;
}
.redactor_toolbar li a.fa-redactor-btn {
  display: inline-block;
  padding: 9px 10px 8px 10px;
  line-height: 1;
}
.redactor_toolbar.redactor-toolbar-typewriter {
  box-shadow: none;
  background: rgba(240, 240, 240, 0.9);
}
.redactor_toolbar.redactor-toolbar-typewriter li a:hover {
  outline: none;
  background-color: #1f78d8;
  color: #fff;
}
.redactor_toolbar.redactor-toolbar-typewriter li a:active,
.redactor_toolbar.redactor-toolbar-typewriter li a.redactor_act {
  outline: none;
  background-color: #ccc;
  color: #444;
}
