//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

button {
	max-width: 100%;
}
button {
	position: relative;
	border: 0;
	background: transparent;
	color: inherit;
	border-radius: 0;
	.fa {
		margin-right: 8px;
	}
}
button, button:hover, button:active {
	outline: 0 !important;
}
// Core styles
.btn {
  display: inline-block;
  padding: 5px 25px;
  margin-bottom: 0; // For input.btn
  font-size: @font-size-base;
  line-height: 36px;
  font-weight: @btn-font-weight;
  line-height: @line-height-base;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  border-bottom: 4px solid transparent;
  white-space: nowrap;
  .user-select(none);

  &:focus {
    .tab-focus();
  }

  &:hover,
  &:focus {
    //color: @btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    //.box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: not-allowed;
    pointer-events: none; // Future-proof disabling of clicks
    .opacity(.65);
    .box-shadow(none);
  }

}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  .button-variant(@btn-default-color; @btn-default-bg; @btn-default-border);
}

// Info appears as blue-green
.btn-info {
  .button-variant(@btn-info-color; @btn-info-bg; @btn-info-border);
}


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: @link-color;
  font-weight: normal;
  cursor: pointer;
  border-radius: 0;

  &,
  &:active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    .box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: underline;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: @btn-link-disabled-color;
      text-decoration: none;
    }
  }
}


// Button Sizes
// --------------------------------------------------


.btn-xs {
  padding: 1px 5px;
}

@media (max-width: 639px) {
	.btn-lg {
		//.btn-sm !important;
		//font-size: 18px !important;
		//line-height: 18px;
		.earn {
			top: 6px;
		}
	}
}

// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}



/*=======================*//*=======================*//*=======================*//*=======================*/
// Buttons
/*=======================*//*=======================*//*=======================*//*=======================*/


.btn{
	font-size: @font-size-sm;
	border-radius: 0 !important;
	letter-spacing: 1px;
	display: inline;
}


.btn-action {
	border-top: none;
	border-right: none;
	border-left: none;
	border-bottom: 7px solid @brand-button-dark;
	&:active {
		border-top: 4px solid #1e9cc7;
		background: #1dacd9;
		border-bottom: 3px solid @brand-button-dark;
		.earn {
			top: 1px;
		}
	}
	&.btn-danger:active {
		border-top: 4px solid @brand-danger-button;
		background: @brand-danger-button-dark;
		border-bottom: 3px solid @brand-danger-button-dark;
	}
}

.btn-primary {
 	border-color: @brand-button-dark;
 	background: @brand-button;
}
.btn-danger {
	background-color: @brand-danger-button;
	border-color: @brand-danger-button-dark !important;
	&:hover {
		border-color: darken(@brand-danger-button-dark, 10%) !important;
	}
	&:active {
		border-top: 4px solid @brand-danger-button-dark;
		border-bottom: 3px solid @brand-button-dark;
	}
}

.btn-default{
	.btn;
	.btn-primary;
	.btn-action;
	.centered;
}



/*=======================*//*=======================*//*=======================*//*=======================*/
// Button Sizes
/*=======================*//*=======================*//*=======================*//*=======================*/

.btn-xs {
	font-size: @font-size-xs !important;
	&.btn-action {
		border-bottom-width: 4px;
		&:active {
			border-top-width: 3px;
			border-bottom-width: 1px;
		}	
	}
}

.btn-sm {
	font-size: @font-size-sm;
	padding: 2px 45px !important;
}
.btn-md {
	font-size: 30px;
	padding: 10px 60px;
	.push-down-lg;
}
.btn-lg {
	font-size: 42px;
	padding: 15px 60px;
	border-bottom-width: 6px;
	.push-down-lg;
}
@media (max-width: 767px) {
	.btn-sm {
		padding: 2px 25px !important;
	}
	.btn-lg {
		font-size: 22px !important;
		padding: 10px 30px;
	}
}
@media (max-width: 639px) {
	.btn-lg {
		padding: 5px 23px;
	}
}


.btn-xl {
	font-size: @font-size-xl;
	padding: 10px 14%;
	border-bottom-width: 6px;
}



.btn-outline {
	border: 2px solid @brand-primary;
	background: white;
	color: @brand-primary;
	&:hover {
		background: @brand-primary;
		color: white;
	}
}
.btn-unstyled {
	background: transparent;
	border: 0;
	outline: none;
}
.btn-small-gray {
	border: 2px solid #d4d4d4;
	color: @gray-dark;
	background: transparent;
	&:hover {
		background: @gray;
		color: black;
	}
}
.btn-edit {
	.btn-sm;
	font-size: @font-size-sm !important;
	padding: 2px 45px !important;
	border: 2px solid #d4d4d4 !important;
	color: @gray-dark;
	background: @gray !important;
	display: inline-block !important;
	background: transparent;
	&:hover {
		background: white;
		color: black;
	}
}

.btn-primary {
  .button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border);
  
}
// Warning appears as orange
.btn-warning {
  .button-variant(@btn-warning-color; @btn-warning-bg; @btn-warning-border);
}
// Danger and error appear as red
.btn-danger {
  .button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border);
}
// Success appears as green
.btn-success {
  .button-variant(@btn-success-color; @btn-success-bg; @btn-success-border);
  border-color: @brand-success-dark;
  &:active {
	background: @brand-success;
  	border-color: @brand-success-dark;
  }
}



