//
// Typography
// --------------------------------------------------

@font-face {
    font-family: 'bebas_neueregular';
    src: url('../fonts/BebasNeue-webfont.eot');
    src: url('../fonts/BebasNeue-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/BebasNeue-webfont.woff') format('woff'),
         url('../fonts/BebasNeue-webfont.ttf') format('truetype'),
         url('../fonts/BebasNeue-webfont.svg#bebas_neueregular') format('svg');
    font-weight: normal;
    font-style: normal;
}
body,p,h1,h2,h3,button {
	//max-width: 100%; 
	-webkit-font-smoothing: antialiased;
}
body {
	font-size: 16px;
	color: #676767;
	line-height: 30px;
	color: #676767; 
	font-weight: 400;
	letter-spacing: 0.025em;
	@media (min-width: 400px) {
		font-size: 18px;
	}
	@media (min-width: 640px) {
		font-size: 22px;
	}
}
a {
	color: @brand-button-dark;
	&:hover {
		color: @brand-button;
		text-decoration: none;
	}
}
p {  
	margin: 0 0 15px;
	@media (min-width: 540px) {
		margin-bottom: 40px;
	}
}
.lead {
  margin-bottom: @line-height-computed;
  font-size: 22px;
  font-weight: 200;
  line-height: 1.4;
  @media (min-width: 768px) {
    font-size: 32px;
  }
}



// Headings
// -------------------------

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: @headings-font-family;
  font-weight: @headings-font-weight;
  line-height: @headings-line-height;
  margin-top: 0;
  color: @gray-dark;
  small {
    font-weight: normal;
    line-height: 1;
    color: @headings-small-color;
  }
}
h1 {
	font-size: 36px;
	@media (min-width: 640px) {
		font-size: 45px;
	}
	@media (min-width: 768px) {
		font-size: 60px;
	}
	margin-top: 0;
	font-family: 'bebas_neueregular';
	margin-top: 0;
	margin-bottom: 50px;
	letter-spacing: .08em;
	color: @gray-dark;
	font-weight: 200;
	text-align: center;
}
h2 {
	font-size: 20px;
	line-height: 38px;
	padding-bottom: 20px;
	@media (min-width: 640px) {
		font-size: 30px;
		padding-bottom: 30px;
	}
	@media (min-width: 768px) {
		font-size: 36px;
		line-height: 55px;
	}
	font-weight: 500;
	border-bottom: 2px solid @brand-primary;

	text-align: left;
	margin-top: 0;
}
h3,h4,h5,h6 {
	font-family: 'bebas_neueregular';
	color: @gray-dark;
	letter-spacing: .11em;
	margin: 0;
	line-height: 40px;
	font-size: 30px;
}
h1 small, .h1 small { font-size: ceil(@font-size-base * 1.70); } // ~24px
h2 small, .h2 small { font-size: ceil(@font-size-base * 1.25); } // ~18px
h3 small, .h3 small,
h4 small, .h4 small { font-size: @font-size-base; }




label {
	font-weight: normal;
	color: @gray-medium;
}
input + label {
