/*=======================*//*=======================*//*=======================*//*=======================*//*=======================*//*=======================*/
/* Animation
/*=======================*//*=======================*//*=======================*//*=======================*//*=======================*//*=======================*/

.counters .icon{
	position: relative;
}
.counters button.active {
	.animated {
		top: -18px;
		opacity: 0;
	}
}
.counters button {
	.animated{
		top: 5px;
		left: 39px;
		opacity: 1;
		-webkit-transition: all  .2s;
		position: absolute;
	}
}

.header, #fileObj, .face, .flash, .in {
	-webkit-transition: all  .2s;
}

.response-count {
	-webkit-transition: background  .2s;
}

.share-button {
	-webkit-transition: color .2s, background-color .2s, border-color .2s, box-shadow .2s !important;
	//transition: box-shadow linear .3s;
}

.sa-points, .notifications-toggle, .user-menu, .menu-open{
	-webkit-transition: all .2s;
	-webkit-transform: scale(.95);
	&:hover {
		-webkit-transform: scale(1);
	}
}
.menu-left li {
	-webkit-transition: all .2s;
	-webkit-transform: scale(.9);
	&:hover {
		-webkit-transform: scale(1);
	}
}


