/* Sorting
/*=======================*//*=======================*//*=======================*/
.sorting-wrap {
	.label {
		color: @gray-dark;
		line-height: @font-size-lg;
	}
	margin-bottom: 10px;
	.centered;
	.clearfix;
	.sort-module {
		.clearfix;
		.centered;
		display: block;
		width: 700px;
		max-width: 95%;
		text-align: left;
		margin: 0 auto 20px auto;
		position: relative;
		.font-size-xs;
		line-height: @font-size-xs;
		color: white;
		.dropdown-toggle {
			float: right;	
			padding: 10px;	
		}
		.selection {
			color: white;
		}
		.btn-group {
			position: static;
			display: inline;
			text-align: left;
			button {
				padding-left: 11px;
				width: 100%;
				.label {
					float: left;
				}
				.selection {
					float: right;
				}
				.caret {
					float: left;
				}
			}
			.dropdown-menu {
				.font-size-xs;
				right: 0;
				background: @brand-button-darker;
				border-radius: 0;
				margin: 0;
				border: 0;
				box-shadow: none;
				border-bottom-right-radius: 5px;
				border-bottom-left-radius: 5px;
				a, a:hover {
					color: white;
					background: transparent;
				}
				li:hover {
					background: darken(@brand-button-darker, 5%);
				}
			}
			.dropdown-toggle {
				border-radius: 5px;
				background: @brand-button-darker;
				color: white;
			}
			&.open {
				
				.dropdown-toggle {
					box-shadow: none;
					border-bottom-right-radius: 0;
					border-bottom-left-radius: 0;
				}
			}
		}
	}
	.sort-post-type {
		li {
			position: relative;
			padding-left: 8px;
			a {
				padding-left: 28px;
			}
			&:before {
				content: '';
				display: block;
				position: absolute;
				top: 7px;
				width: 20px;
				height: 20px;
			}
		}
		.sort-conversations:before {
			.convo-icon-sm;
		}
		.sort-stories:before {
			.story-icon-sm;
		}
		.sort-polls:before {
			.poll-icon-sm;
		}
		.sort-images:before {
			.image-icon-sm;
		}
		.sort-videos:before {
			.video-icon-sm;
		}
		.sort-links:before {
			.link-icon-sm;
		}
	}
}
@media (min-width: 992px) {
	.sorting-wrap {
		width: 500px;
		.sort-module {
			margin: 0 20px 10px 0;
			float: left;
			width: 230px;
		}

